

@mixin text-outline($radius: 1px, $color: #000) {
    text-shadow:
        0 #{$radius} 0 #{$color},
        0 -#{$radius} 0 #{$color},
        -#{$radius} 0 0 #{$color},
        #{$radius} 0 0 #{$color};
}


@mixin paper($depth: 1) {
  @if $depth == 1 {
    box-shadow: 
      0 2px 2px 0 rgba(0,0,0,.14),
      0 1px 5px 0 rgba(0,0,0,.12),
      0 3px 1px -2px rgba(0,0,0,.2);
  }
  @else if $depth == 2 {
    box-shadow: 
      0 4px 5px 0 rgba(0,0,0,.14),
      0 1px 10px 0 rgba(0,0,0,.12),
      0 2px 4px -1px rgba(0,0,0,.4);
  }
  @else if $depth == 3 {
    box-shadow: 
      0 6px 10px 0 rgba(0,0,0,.14),
      0 1px 18px 0 rgba(0,0,0,.12),
      0 3px 5px -1px rgba(0,0,0,.4);
  }
  @else if $depth == 4 {
    box-shadow: 
      0 8px 10px 1px rgba(0,0,0,.14),
      0 3px 14px 2px rgba(0,0,0,.12),
      0 5px 5px -3px rgba(0,0,0,.4);
  }
  @else if $depth == 5 {
    box-shadow: 
      0 16px 24px 2px rgba(0,0,0,.14),
      0 6px 30px 5px rgba(0,0,0,.12),
      0 8px 10px -5px rgba(0,0,0,.4);
  }
}


.icon-link {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &.disabled {
      color: $text-muted;
      cursor: not-allowed;
    }

    &:hover {
      background-color: $gray-lightest;
      text-decoration: none;
    }
        
    i, span {
      display: block;
    }

    i {
      // opacity: .2;
      color: rgba(darken($link-color, 10), .2);
      width: 4rem;
      text-align: center;
      padding: 1rem 0;
      flex-shrink: 0;

      &.fa-external-link {
        width: 2rem;
      }
    }

    span {
      text-align: left;
      flex-grow: 1;
      line-height: 1;
    }
}
